		 */
		insertScriptTag(url) {
			const _this = this;
			let playerScriptTag = document.getElementById('playerScriptTag');
			// å¦æè¿ä¸ªtagä¸å­å¨ï¼åçæç¸å³ä»£ç tagä»¥å è½½ä»£ç 
			if (playerScriptTag === null) {
